  line-height: 36px;
  color: rgba(56, 44, 42, 0.36);
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

.review {
  border-bottom: 2px solid #e8c3c4;
  font-size: 17px;
  color: #252525;
  line-height: 24px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 75px;
  position: relative;
  margin-bottom: 10px;
}
.review:last-child {
  margin-bottom: 0px;
  border-bottom: none;
}

.review__quote {
  margin: 0;
  font-size: 17px;
  line-height: 24px;
}

.review__text {
  margin: 0;
  margin-bottom: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.review__details {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
  line-height: 27px;
  color: rgba(0, 0, 0, 0.4);
}

.review__author {
  font-style: normal;
  font-weight: bold;
}

.review__date {
  -ms-flex-negative: 0;
  flex-shrink: 0;
}
.review__date::before {
  content: '·';
  margin-left: 4px;
  margin-right: 4px;
}

.review__rating {
  position: absolute;
  right: 0;
  top: 22px;
  background: rgba(255, 255, 255, 0.24);
  border-radius: 8px;
  width: 55px;
  text-align: center;
  font-size: 19px;
  color: #353535;
  font-weight: 500;
  line-height: 25px;
  padding: 5px;
}

.sign-in {
  max-width: 480px;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 165px;
}

.sign-in__form {
  margin-bottom: 37px;
}

.sign-in__fields {
  margin-bottom: 23px;
}

.sign-in__field {
  margin-bottom: 3px;
  position: relative;
}
.sign-in__field:last-child {
  margin-bottom: 0;
}
.sign-in__field--error .sign-in__input,
.sign-in__field--error .sign-in__input:focus {
  border-color: #a8421e;
}
.sign-in__field--error .sign-in__error-message {
  display: block;
}

.sign-in__input {
  display: block;
  width: 100%;
  background: #2b1b1b;
  border-radius: 8px;
  border: 1px solid transparent;
  font-size: 30px;
  color: #dfcf77;
  line-height: 44px;
  font-weight: 300;
  padding: 25px 20px;
  -webkit-transition: border-color 0.2s;
  transition: border-color 0.2s;
}
.sign-in__input::-webkit-input-placeholder {
  opacity: 1;
  color: rgba(223, 207, 119, 0.27);
}
.sign-in__input:-ms-input-placeholder {
  opacity: 1;
  color: rgba(223, 207, 119, 0.27);
}
.sign-in__input::-ms-input-placeholder {
  opacity: 1;
  color: rgba(223, 207, 119, 0.27);
}
.sign-in__input::placeholder {
  opacity: 1;
  color: rgba(223, 207, 119, 0.27);
}
.sign-in__input:focus {
  border-color: #dfcf77;
}

.sign-in__btn {
  display: block;
  width: 100%;
  padding: 24px 40px;
  background: none;
  border: 1px solid rgba(217, 205, 141, 0.41);
  border-radius: 8px;
  -webkit-transition: border-color 0.2s;
  transition: border-color 0.2s;
  font-size: 30px;
  line-height: 44px;
  text-align: center;
  color: #d9cd8d;
  cursor: pointer;
}
.sign-in__btn:disabled {
  border: 1px solid #9e9e9e33;
  color: #4a4839;
  cursor: default;
  pointer-events: none;
}
.sign-in__btn:hover,
.sign-in__btn:focus {
  border: 1px solid rgba(217, 205, 141, 0.7);
}

.sign-in__reset-password {
  text-align: center;
}

.sign-in__link {
  font-size: 22px;
  line-height: 26px;
  color: #574427;
  text-decoration: none;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
.sign-in__link:hover,
.sign-in__link:focus {
  color: #dfcf77;
}

.sign-in__message {
  text-align: center;
  font-size: 22px;
  color: #dfcf77;
  text-align: center;
  line-height: 26px;
  margin-bottom: 42px;
}
.sign-in__message p {
  margin: 0;
}

.sign-in__error-message {
  font-size: 22px;
  line-height: 26px;
  color: #a8421e;
  position: absolute;
  z-index: 1;
  left: calc(100% + 16px);
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 200px;
}

.small-film-card {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  -webkit-transition: 0.2s -webkit-transform;
  transition: 0.2s -webkit-transform;
  transition: 0.2s transform;
  transition: 0.2s transform, 0.2s -webkit-transform;
}
.small-film-card:hover {
  z-index: 3;
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}
.small-film-card video,
.small-film-card img {
  vertical-align: top;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  font-family: 'object-fit:cover';
  object-fit: cover;
}

.small-film-card__image {
  position: relative;
  z-index: 1;
  height: 175px;
}
.small-film-card__image::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(25%, rgba(0, 0, 0, 0)),
    color-stop(97%, rgba(0, 0, 0, 0.63))
  );
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0.63) 97%);
}

.small-film-card__mylist {
  position: absolute;
  z-index: 1;
  top: 5px;
  right: 5px;
}

.small-film-card__title {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px 15px;
  margin: 0;
  font-size: 17px;
  line-height: 20px;
  font-weight: 500;
}

.small-film-card__link {
  display: block;
  text-decoration: none;
  color: inherit;
}

.user-block {
  display: flex;
  align-items: center;
  margin-left: auto;
  list-style-type: none;
}

.user-block__item{
  position: relative;
}

.user-block__item:not(:last-child) {
  margin-right: 15px;
}

.user-block__avatar {
  position: relative;

}
.user-block__avatar-wrap {
  display: block;
  position: relative;
  margin: 0 auto;
  width: 63px;
  height: 63px;
  overflow: hidden;
  border-radius: 50%;
  transition: 0.2s transform;
}
.user-block__avatar-wrap:hover {
  transform: scale(1.05);
}
.user-block__avatar img {
  vertical-align: top;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  font-family: 'object-fit:cover';
  object-fit: cover;
}
.user-block__count {
  pointer-events: none;
  position: absolute;
  z-index: 1;
  left: 0;
  bottom: 0px;
  display: inline-block;
  min-width: 28px;
  height: 20px;
  margin-left: 0px;
  padding: 5px;
  font-size: 16px;
  font-weight: 600;
  line-height: 10px;
  color: #fff;
  background: #fd612ead;
  border-radius: 15px;
  text-align: center;
}
.user-block__name {
  font-size: 16px;
  display: block;
  text-align: center;
}
.user-block__link {
  display: block;
  font-size: 20px;
  line-height: 24px;
  color: #dfcf77;
  font-weight: bold;
  text-decoration: none;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
.user-block__link:hover {
  color: #c9b37e;
}

.user-block__avatar,
.user-block__link {
  cursor: pointer;
}

.user-page {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#170202), to(#0c0101));
  background-image: linear-gradient(-180deg, #170202 0%, #0c0101 100%);
  color: #c9b37e;
  padding: 15px;
  padding-bottom: 70px;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.user-page__head {
  margin-bottom: 40px;
  width: 100%;
  position: relative;
}

.user-page__content {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  width: 100%;
}

.user-page__title {
  position: absolute;
  left: 200px;
  right: 200px;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.user-page__film-count {
  position: absolute;
  top: 6px;
  z-index: 1;
  display: inline-block;
  min-width: 40px;
  height: 25px;
  margin-left: 10px;
  padding: 5px;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  text-align: center;
  color: #fff;
  background: #fd612ead;
  border-radius: 15px;
}
